<template>
  <div>
    <img id="logo" alt="Quiz logo" src="../assets/quiz@192px.png">

    <!-- Game not started -->
    <div id="box">
    <b-form @submit="onSubmit">

      <b-card class="mt-3">

        <b-form-group id="input-group-1" label="Your Name:" label-for="input-2">
          <b-form-input
            id="input-2"
            v-model="name"
            placeholder="Please enter your name"
          ></b-form-input>
        </b-form-group>

        <b-form-group id="input-group-2" label="Pick a category:" label-for="input-3">
          <b-form-select
            id="input-3"
            v-model="category"
            :options="categories"
          ></b-form-select>
        </b-form-group>

      </b-card>
      <b-button type="submit" variant="primary">Play</b-button>


    </b-form>

    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        name: null,
        category: 'Any category',
        categories: [
          'Any category',
          'Entertainment',
          'Science',
          'Animals']
      }
    },
    methods: {
      onSubmit(event) {
        event.preventDefault()
        this.$store.state.playerName = this.name
        this.$store.commit('resetGame')
        this.$router.push('/trivia/play')
      },
    }
  }
</script>

<style scoped>
  #box {
    padding: 2em 2em;
  }

  #input-group-1, input-group-2 {
    padding: 1em;
  }

</style>