<template>
    <div v-if="triviaResults && getQuestion">

    <h4>
      <div>Welcome, {{ getPlayerName }}!</div>
      <div>You have {{ getPlayerScore }} points</div>
    </h4>
    
    <h1>Question #{{ getQuestionIndex+1 }} of {{ triviaResults.length }}</h1>
    <h2 v-html="getQuestion"></h2>
    
    <b-button-group class="buttonGroup">
      <b-button
        v-for="(choice,key) in getChoices"
        :key="key"
        class="choiceButton"
        @click="onChoiceButtonClick"
          >{{choice}}</b-button>
    </b-button-group>

    <span v-if="userAnswer">
      <h3 v-if="userAnswer === getAnswer">Correct</h3>
      <h3 v-else>Incorrect</h3>
    </span>
   
  </div>
</template>

<script>
export default {
  name: 'OpenTriviaDb',
  props: {
    category: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      triviaResults: null,
      userAnswer: null,
      nextQuestion: null,
    }
  },
  methods: {
    onChoiceButtonClick(e) {
      if (!this.userAnswer) {
        this.userAnswer = e.target.textContent
        if (this.userAnswer === this.getAnswer) {
          this.$store.commit('addScore')
        }
        this.revealAnswer()
      }
    },
    changeQuestion() {
      this.clearAnswer()
      this.nextQuestion++
      this.countDown()
    },
    revealAnswer() {
      let buttons = document.getElementsByClassName('choiceButton')
      console.log(buttons)
      for (var n=0; n<buttons.length; n++) {
        var buttonAnswer = buttons[n].textContent.trim()
        buttons[n].classList.add(
          this.getAnswer === buttonAnswer
            ? 'bg-success'
            : 'bg-danger'
      )}
      setTimeout(() => this.changeQuestion(), 2000);
    },
    clearAnswer() {
      this.userAnswer = null
      let buttons = document.getElementsByClassName('choiceButton')
      for (var n=0; n<buttons.length; n++) {
        buttons[n].classList.remove('bg-success')
        buttons[n].classList.remove('bg-danger')
      }
    },
    filterResultsByCategory(results) {
      console.log('Filtering')
      return this.category
        ? results.filter(q => 
          q.category.toLowerCase().indexOf(this.category.toLowerCase()) !== -1)
        : results
    },
  },
  watch: {
    triviaResults() {
      this.nextQuestion = 0
      this.countDown()
  }
  },
  computed: {
    getPlayerName() {
      return this.$store.state.playerName
    },
    getPlayerScore() {
      return this.$store.state.playerScore
    },
    getQuestionIndex() {
      return this.nextQuestion
    },
    getQuestionData() {
      return this.getQuestionIndex < this.triviaResults.length
        ? this.triviaResults[this.getQuestionIndex] : null
    },
    getQuestion() {
      let data = this.getQuestionData
      return data ? data.question : null
    },
    getAnswer() {
      let data = this.getQuestionData
      return data ? data.correct_answer : null
    },
    getChoices() {
      let data = this.getQuestionData
      if (data) {
        var choices = data.incorrect_answers
        var insertAt = Math.floor(Math.random() * choices.length)
        choices.splice(insertAt, 0, data.correct_answer);
        return choices
      }
      else {
        return []
      }
    }
  },
  async mounted () {
    if (!this.getPlayerName) {
      this.$router.push('/trivia/')
      return
    }
    fetch('https://opentdb.com/api.php?amount=5')
      .then(response => response.json())
      .then(result => {
        if (result.response_code !== 0) {
          console.log('Failed to fetch trivia questions')
        }
        else {
          this.triviaResults = this.filterResultsByCategory(result.results)
        }
      })
  },
}
</script>

<style scoped>
  .buttonGroup {
    width: 90%;
  }
  h3, h4 {
    margin-top: 2em;
  }
</style>
